.DishesCarousel {
  display: flex;
  grid-column-start: 1;
  grid-column-end: 3;
  align-items: center;
}

.DishesCarousel .dishes-container {
  display: flex;
  gap: 20px;
  overflow-x: hidden ;
}

.DishesCarousel .arrow {
  width: 48px;
  height: 48px;
}
.DishesCarousel .dishes-container .dish{
  padding: 20px;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
} 
.DishesCarousel .dishes-container .active.dish {
  background-color: white;
}
.DishesCarousel .dishes-container .dish img {
  width: 160px;
}
.DishesCarousel .dishes-container .dish h3 {
  margin-top: 10px;
  text-align: center;
  font-weight: 500;
}
@media screen and (max-width: 1350px) {
  .DishesCarousel {
    margin-top: 120px;
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 1050px) {
  .DishesCarousel {
    grid-column-end: 2;
  }
}
