.DishTitle {
  position: relative;
  grid-column-start: 2;
  grid-column-end: 3;
}
.DishTitle .order-food {
  display: flex;
  gap: 10px;
}
.DishTitle h2 {
  text-transform: uppercase;
  font-size: 80px;

}
.DishTitle h2::first-line {
  font-weight: 200;
}
.DishTitle .order-food p {
  font-size: 24px
}
.DishTitle .order-food img {
  height: 24px;
  width: 24px;
}
@media screen and (max-width: 1050px) {
  .DishTitle {
    position: relative;
    grid-column-start: 1;
    grid-column-end: 2;
  }
}
