.header {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: end;
  align-items: center;
  height: 80px;
}
header nav {
  display: none;
}
header img {
  width: 48px;
  height: 48px;
}

@media screen and (max-width: 1050px) {
  .header {
    grid-column-end: 2;
  }
}
