.DishDescription {
  position: relative;
  background-color: white;
  width: 100%;
  padding: 48px;
  border-radius: 40px;
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 4;
  align-self: start;
}
.DishDescription .nav{
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}
.DishDescription .nav p {
  font-size: 20px;
  font-weight: 500;
}
.DishDescription .star {
  width: 24px;
  height: 24px;
}
.DishDescription .rating {
  margin-bottom: 20px;
  width: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFED50;
  border-radius: 24px;
  padding: 0px 15px 15px;
}
.DishDescription .rating p {
  margin: 0;
  transform: translateX(35px);
  font-size: 80px;
  font-weight: 700;
}
.DishDescription h3 {
  font-size: 36px;
}
.DishDescription small {
  font-size: 20px;
}
.DishDescription p {
  font-size: 20px;
}
.DishDescription .rate-buttons {
  margin-top: 20px;
  display: flex;
  gap: 20px;
}
.DishDescription .icon-rounded {
  background-color: #F0EFEF;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.DishDescription .icon-rounded:hover {
  background-color:#FFED50;
}
.DishDescription .icon-rounded img {
  width: 36px;
  height: 36px;
}

@media screen and (max-width: 1350px) {
  .DishDescription{
    grid-row-start: 4;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}
@media screen and (max-width: 1050px) {
  .DishDescription{
    grid-column-end: 2;
  }
}
