.ImagesCarousel {
  overflow: hidden;
  position: absolute;
  top: -1160px;
  left: -420px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  padding-bottom: 100px;
}
.ImagesCarousel > img {
  width: 1500px;
  height: 1500px;
  grid-area: 1/1/2/2;
  aspect-ratio: 1;
}
.ImagesCarousel .images {
  width: 100%;
  height: 100%;
  grid-area: 1/1/2/2;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
}
.ImagesCarousel .images img {
  width: 400px;
  height: 400px;
  position: relative;
  top: -85px;
}
.ImagesCarousel .images div {
  grid-area: 1/1/2/2;
  display: flex;
  align-items: start;
  justify-content: center;
}

.ImagesCarousel .images .active {
  transform: rotate(185deg);
  transition: 2s;
}

.ImagesCarousel .images .active img {
  transform: rotate(-185deg);
  transition: 2s;
}

@media screen and (max-width: 1050px) {
  .ImagesCarousel {
    left: calc((100vh - 1500px)/2);
    width: 100%;
  }
  .ImagesCarousel .images .active {
    transform: rotate(180deg);
    transition: 2s;
  }
  
  .ImagesCarousel .images .active img {
    transform: rotate(-180deg);
    transition: 2s;
  }
}
